import React from 'react'
import { Row, Col } from 'antd'

import { checkMobile } from 'helper/helper'
import PowerBallConentMain from 'moduls/main/PowerBallConentMain'
import LottoConentMain from 'moduls/main/LottoConentMain'
import RewardSpy from 'moduls/main/RewardSpy';
import { LayoutLottery } from "components";
import "./style.scss";



const HomeScreen = () => {
    const isMobile = checkMobile()

    return (
        <LayoutLottery>
            <div id="home-page" className="container-cst mt-3">
                <div className="home-cont">
                    <Row>
                        <Col span={24} className="pb-2">
                            <RewardSpy />
                        </Col>
                        <Col xxl={14} xl={14} lg={14} md={14} span={24} className={!isMobile ? "pr-2" : ''}>
                            <PowerBallConentMain isMobile={isMobile} />
                        </Col>
                        <Col xxl={10} xl={10} lg={10} md={10} span={24}>
                            <LottoConentMain isMobile={isMobile} />
                        </Col>
                    </Row>
                </div>
            </div>
        </LayoutLottery>
    )
}

export default HomeScreen
