import React, { useState, useEffect } from "react";
import { Row, Col } from "antd";
import { connect } from "react-redux";

import { CaretRightOutlined } from "@ant-design/icons";
import { BoxLottery } from "components";
import { checkMobile } from "helper/helper";
import "./style.scss";

const RewardSpy = ({ getMoneyOfGame, dataMoneyGame }) => {
  const formatAmount = new Intl.NumberFormat("ko-KR");
  const isMoblie = checkMobile();
  const [data, SetData] = useState({});
  // const data = {};

  useEffect(() => {
    getDataTimeout();
  }, []);

  const getDataTimeout = async () => {
    await getMoneyOfGame().then(() => {
      setTimeout(() => getDataTimeout(), 5000);
    });
  };

  useEffect(() => {
    if (dataMoneyGame.length) {
      const _data = {};

      for (const [i, _] of [...dataMoneyGame].entries()) {
        if (!_) {
          continue;
        }

        if (_data[_.type] === undefined) {
          _data[_.type] = [];
        }

        _data[_.type].push((appendedClass = "") => (
          <div
            className={['flex-center', 'justify-between', ...appendedClass.split(' '), ...(isMoblie ? ['pt-2', 'pb-2'] : ['pd-5'])].join(' ')}
            key={String(`______key-main___${i}`)}
          >
            <div className="d-flex align-center mr-1">
              <div style={{ width: (!isMoblie && "auto") || 45 }}>
                <img
                  height={isMoblie ? 32 : 25}
                  src={require(`resources/images/${
                    _.type !== "LOTTO" ? "Logo_game_pow" : "loto-tran"
                  }.png`)}
                />
              </div>
              <span className={`pl-${(isMoblie && 2) || 3}`}>
                {(_.type === "LOTTO" ? "7일 " : "") + (_?.name_game || "N/A")}
                {
                  ((
                    _.type === "LOTTO"
                    ? (_.roundofgame ?? _.roundofgame)
                    : (_.roundofdate ?? _.roundofgame)
                  ) ?? "N/A").toString()
                }
                회
              </span>
            </div>
            <span className="bold f-14">
              {isMoblie ? "" : "총 당첨금 "}
              {formatAmount.format(_.amount ?? 0)}원
            </span>
          </div>
        ));
      }

      SetData({ ..._data });
    }
  }, [dataMoneyGame]);

  return (
    <div className={`reward-spy${isMoblie ? '-mobile' : ""}`}>
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        {(isMoblie && (
          <div
            class="pd-5"
            style={{
              width: "100%",
              display: "flex",
              "flex-direction": "column-reverse",
            }}
          >
            <input id="show_more" type="checkbox" class="check-show-more" />
            <label class="label-show-more text-center" for="show_more">
              더보기
              <CaretRightOutlined rotate={90} className="ml-1" />
            </label>
            <label class="label-show-less text-center" for="show_more">
              접기
              <CaretRightOutlined rotate={-90} className="ml-1" />
            </label>
            <div class="content-show-more">
              {data.POWERBALL?.map((generate, i) =>
                generate(
                  i === data.POWERBALL.length - 1
                    ? undefined
                    : " hidden-show-more"
                )
              )}
              {data.LOTTO?.map((generate, i) =>
                generate(
                  i === data.LOTTO.length - 1 ? undefined : " hidden-show-more"
                )
              )}
            </div>
            <h2 class="reward-spy-title">실시간 누적 당첨금</h2>
          </div>
        )) || (
          <>
            <div className="reward-spy-full">
              <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
                <Row span={24}>
                  <Col span={12}>
                    {data.POWERBALL?.map((generate) => generate())}
                  </Col>
                  <Col span={12}>
                    {data.LOTTO?.map((generate) => generate())}
                  </Col>
                </Row>
              </BoxLottery>
            </div>
            <Row span={24}>
              <Col span={12}>
                {data.POWERBALL?.[data.POWERBALL.length - 1]()}
              </Col>
              <Col span={12}>{data.LOTTO?.[data.LOTTO.length - 1]()}</Col>
            </Row>
          </>
        )}
      </BoxLottery>
    </div>
  );
};

export default connect(
  (state) => {
    const { main = {} } = state;
    return {
      dataMoneyGame: main?.dataMoneyGame || [],
    };
  },
  ({ main: { getMoneyOfGame } }) => {
    return {
      getMoneyOfGame,
    };
  }
)(RewardSpy);
